document.addEventListener("DOMContentLoaded", function () {
	const tabsBlock = document.querySelector(".tabs-block");
	const tabs = tabsBlock.querySelectorAll(".tabs-block__tab");
	const tabContents = tabsBlock.querySelectorAll(".tabs-block__tab-content");

	tabs.forEach((tab, i) => {
		tab.addEventListener("click", function (event) {
			event.preventDefault();
			tabs.forEach((tab) => {
				tab.classList.remove("tabs-block__tab--active");
			});
			tab.classList.add("tabs-block__tab--active");
			tabContents.forEach((tabContent) => {
				tabContent.classList.remove("tabs-block__tab-content--active");
			});
			tabContents[i].classList.add("tabs-block__tab-content--active");
		});
	});
});
